<template>
  <div class="lot">
    <lottie :options="defaultOptions"
      :height="100"
      :width="100"
      v-on:animCreated="handleAnimation" />
  </div>
</template>

<script>
import Lottie from 'vue-lottie';
import animationData from "@a/js/icon-use-json/sj-sjcj.json"
// const requireComponent = require.context('../src/assets/js/icon-use-json', true, /\w+\.(json)$/)
// const icons = {}
// requireComponent.keys().map(fileName => {
//   icons[fileName.replace('./', '').replace('.json', '')] = requireComponent(fileName)
// })
export default {
  name: 'lottie',
  components: {
    'lottie': Lottie
  },
  data () {
    return {
      defaultOptions: { animationData: animationData },
      animationSpeed: 1
    }
  },
  methods: {
    handleAnimation: function (anim) {
      this.anim = anim;
    },

    stop: function () {
      this.anim.stop();
    },

    play: function () {
      this.anim.play();
    },

    pause: function () {
      this.anim.pause();
    },

    onSpeedChange: function () {
      this.anim.setSpeed(this.animationSpeed);
    }

  }
}
</script>

